.header {
    background-color: #263238;
    width: 100%;
    height: 55px;
    padding: 0;
    margin: 0;
}

.title {
    color: white;
    font-size: 18px;
    font-weight: 400;
    display: inline-flex;
    margin: 15px;
    padding: 0;  
    width: max-content;
    vertical-align: middle;
}

#search-field {
    display: inline-flex;
    width: max-content;
    background-color: #c0c0c0;
    border-radius: 4px;
    position: relative;
    vertical-align: middle;
    float: right;
    margin-top: 8px;
    margin-bottom: 8px;
    margin-right: 20px;
}
.searchIcon{
    display: inline-block;
    position: relative;
    vertical-align: middle;
    margin: 4px;
}
.searchInput {
    display: inline-block;
    width: 250px;
}
.header-right {
    display: inline-flex;
    position: relative;
    float: right;
}
#profile-icon {
    width: 2.5rem;
    height: 2.5rem;
}